import React from "react";
import { Link } from "react-router-dom";
import { Dropdown, Menu } from "semantic-ui-react";

export default function MasterMenu() {
  return (
    <Menu.Item>
      <Dropdown pointing="top right" text="">
        <Dropdown.Menu>
          <Dropdown.Item as={Link} to="/members" text="" icon="users" />
          <Dropdown.Item
            as={Link}
            to="/memberaffiliations"
            text=""
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/membertags"
            text=""
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/memberranks"
            text=""
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/memberhubs"
            text=""
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/memberhubcostsaless"
            text=""
            icon="users"
          />
          <Dropdown.Divider />
          <Dropdown.Item
            as={Link}
            to="/clients"
            text=""
            icon="building outline"
          />
          <Dropdown.Divider />
          <Dropdown.Item as={Link} to="/projects" text="" icon="inbox" />
          <Dropdown.Item
            as={Link}
            to="/accdepartments"
            text=""
            icon="chart bar"
          />
          <Dropdown.Item
            as={Link}
            to="/accsubjects"
            text=""
            icon="chart bar"
          />
        </Dropdown.Menu>
      </Dropdown>
    </Menu.Item>
  );
}
